import { TimelineItem, Timeline, TimelineSeparator, TimelineConnector, TimelineContent } from "@mui/lab";
import { BodyWrapper, ContentWrapper, CustomDot, HeaderContentWrapper, HeaderWrapper } from "./timeline.styles";

type Content = {
    label: string;
    color: string;
    icon: any;
}

type TimelineProps = {
    position?: "left" | "right" | "alternate" | "alternate-reverse" | undefined;
    content: Content[];
}

type TimelineItemProps = {
    item: Content;
    isLast?: boolean;
    left?: boolean;
}

export const NewTimeline = ({
    position = "alternate",
    content,
}: TimelineProps) => {
    return(
        <Timeline position={position}>
            {
                content.map((item: Content, i, {length}) => {
                    return(
                        <NewTimelineItem 
                            item={item} 
                            isLast={length-1 === i}
                            left={i%2 === 0}
                        />
                    )
                })
            }
        </Timeline>
    )
}

const NewTimelineItem = ({
    item,
    isLast = false,
    left = true,
}: TimelineItemProps) => {
    console.log(item, left);
    return(
        <TimelineItem>
            <TimelineSeparator>
            <CustomDot dotColor={item.color}>
                {item.icon}
            </CustomDot>
            {!isLast && <TimelineConnector />}
            </TimelineSeparator>
            <TimelineContent>
                <ContentWrapper left={left}>
                    <HeaderWrapper>
                        <HeaderContentWrapper>{item.label}</HeaderContentWrapper>
                        <HeaderContentWrapper>test</HeaderContentWrapper>
                    </HeaderWrapper>
                    <BodyWrapper>
                        This is the description section
                    </BodyWrapper>
                </ContentWrapper>
            </TimelineContent>
        </TimelineItem>
    )
}