import { NewTimeline } from "../../components"
import { PageWrapper, BlueSectionWrapper, PinkSectionWrapper, WhiteSectionWrapper } from "../pages.styles"
import SchoolIcon from '@mui/icons-material/School';

export const Home = () => {
    const testContent = [
        {
            label: "Secondary",
            color: "black",
            icon: <SchoolIcon/>,
        }, {
            label: "Success",
            color: "pink",
            icon: <SchoolIcon/>,
        }
    ];

    return(
        <PageWrapper>
            <BlueSectionWrapper>
                <NewTimeline content={testContent}/>
                Homepage
            </BlueSectionWrapper>
            <PinkSectionWrapper>
                Homepage
            </PinkSectionWrapper>
            <WhiteSectionWrapper>
                Homepage
            </WhiteSectionWrapper>
            <PinkSectionWrapper>
                Homepage
            </PinkSectionWrapper>
            <BlueSectionWrapper>
                Homepage
            </BlueSectionWrapper>
        </PageWrapper>
    )
}